@import url("https://fonts.googleapis.com/css2?family=Playfair:ital,wght@0,400;0,500;0,600;1,400;1,500;1,600&family=UnifrakturMaguntia&display=swap");

:root {
  --clr-white-1: #faf9f0;
  --clr-white-2: #f7f6ee;
  --clr-white-3: #cac9c3;
  --clr-black: #080911;

  --ff-display: "UnifrakturMaguntia", cursive;
  --ff-primary: "Playfair", serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--ff-primary);
  color: var(--clr-black);
}

::selection {
  color: var(--clr-white-1);
  background: var(--clr-black);
}

body {
  overflow: hidden;
  background: var(--clr-white-1);
}

.wh100 {
  height: 100%;
  width: 100%;

  /* temp */
  /* background: #00000010; */
}

.btn {
  transition: background-color 0.1s linear, border-color 0.1s linear;
}

