.Terms {
  font-weight: 500;
  line-height: 1.75rem;
  font-size: 1.5rem;

  /* flex */
  display: flex;
  flex-direction: column;
}

.Terms ol {
  margin-left: 30px;
}

.Terms p,
.Terms li {
  margin-bottom: 16px;
}

.Terms h2 {
  margin-bottom: 16px;
  user-select: none;
}

.terms-link {
  display: none;
  cursor: pointer;
  text-decoration: underline 2px;
}

.agreement {
  overflow-y: auto;
  margin-bottom: 2rem;

  scrollbar-width: thin;
  scrollbar-color: var(--clr-black);
}

/* choice */
.choice-container,
.choice {
  width: 100%;
  display: flex;
}

.choice {
  font-weight: 600;
}

.state-m {
  white-space: nowrap;
}

.sign {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 250px;
  position: relative;
  margin: 0 1rem;
  max-height: 32px;
  cursor: pointer;
}

.sign::after {
  content: "";
  width: 100%;
  height: 2.5px;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 100px;
  background: var(--clr-black);
}

.sign img {
  max-width: 250px;
  position: absolute;
  bottom: 0;
  opacity: 0.15;
  transition: opacity 0.1s linear;
}

.sign:hover img {
  opacity: 0.4;
}

.chosen img {
  opacity: 1;
}

.chosen:hover img {
  opacity: 0.6;
}

@media screen and (max-width: 767px) {
  .agreement,
  .terms-heading {
    display: none;
  }

  .terms-link {
    display: block;
  }
}
