.Login,
.Register {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lg-auth-container {
  width: 80%;
  max-width: 600px;
  min-width: 400px;

  border-left: 4px solid;
  border-right: 4px solid;
  border-color: var(--clr-black);
  border-radius: 4px;

  margin-bottom: 100px;

  /* flex */
  display: flex;
  justify-content: center;

  box-sizing: content-box;
  padding: 48px 0;

  /* temp */
  /* height: 500px; */
}

.inner-auth-container {
  height: 100%;
  width: fit-content;

  /* flex */
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1px;
}

.ac-input-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
}

.ac-input-text-container {
  background: var(--clr-white-2);
  padding: 10px 1rem 15px;
  max-width: 300px;
  height: 60px;
  position: relative;

  /* flex */
  display: flex;
  align-items: center;
}

.ac-input-text {
  all: unset;
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
  spellcheck: false;
}

.ac-input-text-container::after {
  content: "";
  position: absolute;
  bottom: 6px;
  left: 5%;
  background: var(--clr-black);
  min-height: 3px;
  min-width: 90%;
  border-radius: 5px;
}

.ac-input-text-container:hover .ac-input-sp,
.ac-input-text-container:focus .ac-input-sp {
  display: block;
}

/* password */
.ac-input-sp {
  cursor: pointer;
  display: none;
  user-select: none;
}

/* label */

.ac-label {
  margin-left: 5%;
}

.ac-input-btn {
  /* flex */
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 60px;

  font-size: 1.5rem;
  font-weight: 600;

  cursor: pointer;
}

.ac-input-btn span {
  transition: all 0.3s linear;
}

.btn-primary {
  border: 3px solid var(--clr-black);
  background: var(--clr-white-1);
}

.btn-primary span {
  color: var(--clr-black);
}

/* .btn-primary:hover {
  background: var(--clr-black);
} */

/* .btn-primary:hover span {
  color: var(--clr-white-1);
} */

.btn-secondary {
  border: 3px solid var(--clr-white-1);
  background: var(--clr-black);
}

.btn-secondary span {
  color: var(--clr-white-1);
}

.btn-secondary:hover {
  border: 3px solid var(--clr-black);
  background: var(--clr-white-1);
}

.btn-secondary:hover span {
  color: var(--clr-black);
}

/* btn-secondary-disabled */
.btn-secondary-disabled {
  cursor: default;
  border: 3px solid var(--clr-white-3);
  background: var(--clr-white-1);
}

.btn-secondary-disabled span {
  color: var(--clr-white-3);
}

/* tool tip */
.btn {
  position: relative;
  user-select: none;
}

.tool-tip {
  position: absolute;
  font-size: 1rem;
  width: 200px;
  background: var(--clr-black);
  color: var(--clr-white-1);

  left: calc(110%);

  padding: 8px 16px;
  z-index: 1;

  transition: all 0.3s ease-in-out;
  transform: translateY(100px);
  opacity: 0;
}

.tool-tip-active {
  transition: all 0.5s ease-in-out;
  transform: translateY(0);
  opacity: 1;
}

.tool-tip::before {
  position: absolute;
  content: "";
  background-color: var(--clr-black);
  aspect-ratio: 1 / 1;
  width: 16px;
  top: 4px;
  right: calc(100% - 8px);
  z-index: -1;
  transform: rotate(45deg);
}

@media screen and (max-width: 767px) {
  .lg-auth-container {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 500px) {
  .lg-auth-container {
    border: none;
  }
}
