.Home {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  overflow: visible;
  padding-bottom: 8vh;
}

.btn-signOut {
  position: absolute;
  top: 0;
  right: 0;
  transform: scaleX(-1);
  font-size: 32px;
  cursor: pointer;
  transition: all 0.2s linear;
  z-index: 10;
}

.btn-signOut:hover {
  transform: scale(-1.2, 1.2);
}

.home-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 800px;
  height: 100%;
  overflow-x: visible;
  position: relative;
}

.home-header {
  /* background: #00000022; */
  text-align: center;
  width: 100%;
  /* height: 100%; */
  margin-bottom: 32px;
}

.home-header h1 {
  font-family: var(--ff-display);
  text-transform: capitalize;
  font-size: 5rem;
}

.btn-add-task {
  width: 100%;
  height: 48px;
  padding: 0 16px;
  display: flex;
  justify-content: center;
  align-items: stretch;
  font-size: 1.25rem;
  cursor: pointer;
}

.btn-add-task .bat-btn,
.btn-add-task .bat-form {
  display: none;
}

.bat-display {
  text-transform: capitalize;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bat-input-text,
.bat-form {
  all: unset;
  width: 100%;
  text-align: left;
}

.bat-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 0 10px;
}

.bat-add {
  background: var(--clr-black);
}

.bat-add svg {
  stroke: var(--clr-white-1);
}

.add-task {
  cursor: unset;
  padding-right: 0;
  border: 3px solid var(--clr-black);
  background: var(--clr-white-1);
  color: var(--clr-black);
}

.add-task .bat-btn,
.add-task .bat-form {
  display: flex;
}

.add-task .bat-display {
  display: none;
}

/* todo */
.todo-list {
  border: 0 var(--clr-black) solid;
  border-width: 0 4px;
  border-radius: 4px;
  width: 100%;
  height: 100%;
  padding: 0 32px;
  overflow-y: auto;
  /* overflow-x: visible !important; */
}

.todos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* gap: 8px; */
}

.todo {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todo-text-container {
  display: flex;
  align-items: center;
  width: 100%;
  cursor: pointer;
  margin: 8px 0;
}

.todo-no {
  display: inline-block;
  margin-right: 1rem;
  font-weight: 600;
  font-size: 1.5rem;
}

.todo-text {
  margin: 8px 0;
  display: inline-block;
  font-size: 1.25rem;
}

.todo-text-completed .todo-text {
  text-decoration: line-through;
}

.todo-hr {
  all: unset;
  display: block;
  background: var(--clr-white-1);
  height: 2px;
  width: 80%;
  border-radius: 2px;
  transition: all 0.2s ease-out;
}

.todo:hover .todo-hr {
  background: var(--clr-white-3);
}

.todo-selected .todo-hr {
  width: 90%;
  background: var(--clr-black) !important;
}

.todo-toolbar {
  position: absolute;
  z-index: inherit;
  top: 50%;
  left: 100%;
  /* padding: 16px 4px; */
  transition: all 0.1s linear;
  border: 4px solid var(--clr-black);
  background: var(--clr-black);
  border-left: none;
  border-radius: 0 6px 6px 0;
}

.todo-phone-toolbar {
  position: absolute;
  z-index: inherit;
  bottom: 0;
  width: 100vw;
  transition: all 0.1s linear;
  border: 4px solid var(--clr-black);
  background: var(--clr-black);
  display: none;
  align-items: stretch;
  /* justify-content: space-around; */
}

.todo-phone-toolbar div{
  padding: 10px 5px;
  transition: all 0.1s linear;
  cursor: pointer;
  background: var(--clr-black);
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.todo-toolbar div {
  padding: 10px 5px;
  transition: all 0.1s linear;
  cursor: pointer;
  background: var(--clr-black);

  display: flex;
  align-items: center;
  justify-content: center;
}

.todo-toolbar svg,
.todo-phone-toolbar svg {
  font-size: 1.75rem;
  fill: var(--clr-white-1);
  transition: all 0.1s linear;
}

.todo-toolbar div:hover,
.todo-phone-toolbar div:hover {
  background: var(--clr-white-1);
}

.todo-toolbar div:hover svg,
.todo-phone-toolbar div:hover svg {
  fill: var(--clr-black);
}

@media screen and (max-width: 767px) {
  .todo-phone-toolbar{
    display: flex;
  }
  .todo-toolbar{
    display: none;
  }
}
